import React, { useContext } from 'react';
import { Title } from 'core/src/typography';

import TableContext from '../TableContext';
import { Cell, CellProps } from './Cell';

/** Стилизованная ячейка футера таблицы */
export function FooterCell({
  children,
  ellipsis,
  ...rest
}: CellProps): JSX.Element {
  const {
    size
  } = useContext(TableContext);

  const variant = size === 'default' ? 'h3_default' : 'h4_default';

  const footerChildren = (children !== undefined)
  && (typeof children === 'number'
    || typeof children === 'string')
    ? (
      <Title variant={ variant } fontWeight="bold" isEllipsis={ ellipsis }>
        { children }
      </Title>
    )
    : children;

  return (
    <Cell { ...rest } ellipsis={ ellipsis } component="td">
      { footerChildren }
    </Cell>
  );
}
